.footer-color {
    background-color: #f7f7f7;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
    padding: 20px 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 100%;
    margin-top: 30px;
}

.footer-container {
    width: 100%;
    max-width: 95%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.footer-logos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    width: 100%;
}

.footer-row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    flex-wrap: wrap;
    width: 100%;
}

/* Linhas com diferentes quantidades de logos */

.footer-row-5 {
    gap: 25px;
}

/* Para 5 logos */
.footer-row-6 {
    gap: 20px;
}

.footer-row-8 {
    gap: 10px;
}

.footer-logo img {
    height: 50px;
    width: auto;
    transition: transform 0.3s ease;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
}

.footer-logo img:hover {
    transform: scale(1.05);
}

.footer-logo-small img {
    height: 45px;
    max-height: 45px;
}

.footer-logo-medium img {
    height: 65px;
    max-height: 65px;
}

.footer-logo-large img {
    height: 100px;
    max-height: 100px;
}

.footer-logo-xlarge img {
    height: 130px;
    max-height: 130px;
}

.footer-logo-xxlarge img {
    height: 160px;
    max-height: 160px;
}

.footer-divider {
    width: 100%;
    height: 1px;
    background-color: #ccc;
    margin-top: 10px;
}

.footer-copy {
    color: #666;
    font-size: 14px;
    text-align: center;
    padding-top: 10px;
}

.footer-label {
    text-align: center;
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 8px;
    margin-top: 18px;
    letter-spacing: 1px;
}

@media (max-width: 768px) {
    .footer-logo img {
        height: 40px;
    }

    .footer-row {
        gap: 20px;
    }

    .footer-row-2 {
        gap: 40px;
    }

    .footer-row-3 {
        gap: 25px;
    }

    .footer-row-4 {
        gap: 20px;
    }

    .footer-row-5 {
        gap: 15px;
    }

    .footer-container {
        padding: 0 10px;
        max-width: 100%;
    }
        .footer-logo-small img {
        height: 1.8rem;
        max-height: 1.8rem;
    }

    .footer-logo-medium img {
        height: 2.7rem;
        max-height: 2.7rem;
    }

    .footer-logo-large img {
        height: 4rem;
        max-height: 4rem;
    }

    .footer-logo-xlarge img {
        height: 5.2rem;
        max-height: 5.2rem;
    }    
    
    .footer-logo-small img {
        height: 1.8rem;
        max-height: 1.8rem;
    }

    .footer-logo-medium img {
        height: 2.7rem;
        max-height: 2.7rem;
    }

    .footer-logo-large img {
        height: 4rem;
        max-height: 4rem;
    }

    .footer-logo-xlarge img {
        height: 5.2rem;
        max-height: 5.2rem;
    }

    .footer-logo-xxlarge img {
        height: 6.5rem;
        max-height: 6.5rem;
    }
}